<template>
  <div class="top-container">
    <div v-if="project">
      <h1>{{ project.name }}</h1>
      <div v-html="project.details" class="details"></div>
      <div class="code-cards">
        <div v-for="(code, index) in project.code" :key="index" class="code-card">
          <div class="code-header" @click="toggleSnippet(index)">
            <h3>{{ code.title }}</h3>
            <span v-if="!showSnippet[index]">+</span>
            <span v-else>-</span>
          </div>
          <div v-if="showSnippet[index]" class="code-body">
            <p>{{ code.description }}</p>
            <pre><code>{{ code.snippet }}</code></pre>
          </div>
        </div>
      </div>
      <img v-for="image in project.images" :key="image" :src="require(`../${image}`)" alt="Project Image" />
    </div>
    <div v-else>
      <p>No data, apologies for that.</p>
    </div>
  </div>
</template>

<script>
import projects from '../data/projects.json';

export default {
  data() {
    return {
      project: null,
      showSnippet: []
    };
  },
  mounted() {
    console.log('ProjectDetails.vue mounted');
    const projectId = this.$route.params.id;
    this.project = projects.find(project => project.id === parseInt(projectId));
    this.showSnippet = new Array(this.project.code.length).fill(false);
  },
  methods: {
    toggleSnippet(index) {
      this.showSnippet[index] = !this.showSnippet[index];
    }
  }
};
</script>

<style scoped>
.top-container {
  margin-top: 6rem;
  text-align: center;
}

.code-cards {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.code-card {
  width: 50%;
  margin: 0 15px;
  border: 1px solid #0facdb;
  border-radius: 5px;
  margin-bottom: 10px;
  box-shadow: 0px 0px 20px rgba(3, 79, 141, 0.733);
}

.code-header {
  background-color: #0facdb;
  padding: 10px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.code-header:hover {
  background-color: rgb(247, 111, 0);
}

.code-body {
  padding: 10px;
}

.code-body p {
  margin-bottom: 10px;
}

pre {
  white-space: pre-wrap;
}

code {
  display: block;
  overflow-x: auto;
}

h1 {
  font-family: "Nunito Sans", sans-serif;
  box-shadow: 0 3px 0 0;
  margin: 0 auto; 
  width: 50%; 
  text-align: center;
}

p, .code-body p {
  font-family: "KoHo", sans-serif;
}

pre code {
  text-align: left;
}

img {
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.767);
  border-radius: 10px;
  margin: 20px auto; 
  display: block; 
}

.details {
  font-family: "Nunito Sans", sans-serif;
  margin: 0 auto; 
  width: 50%; 
  text-align: left; 
  font-weight: 700;
  font-size: large;
}

h3{
  font-family: "Nunito Sans", sans-serif;
  margin: 0 auto;  
  color: white;
}
</style>
